import React, {useState, MouseEvent} from 'react';
import './style.module.less';
import { ProfileData } from './data.d';
import { Row, Col, Button, Modal, Alert, Icon, message } from 'antd';
import MyDropzone from './components/Dropzone';
import request from 'utils/request';
import configs from 'utils/config';
import StandardTable, {StandardTableColumnProps} from 'components/StandardTable';

interface profileProps {
  profile: ProfileData;
  loading: boolean;
}

const Record: React.FC<profileProps> = ({ loading, profile }) => {

  const [file, setFile] = useState<any>()
  const [upStatus, setUpStatus] = useState({uploaded: false, failNum: 0, successNum: 0, totalNum: 0})
  const handleSubmit = async () => {
    const res  = await request({
      url: '/file/importStuModel',
      method: 'post',
      data: file,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    console.log('res', res)
    if (res && res.status === 200) {
      setUpStatus({
        uploaded: true,
        ...res.data
      })
    }
  }
  const downloadTemplateFile = async (e: MouseEvent) => {
    e.preventDefault();
    const res = await request({
      url: '/file/downloadStuModel',
      method:'get'
    })
    if (res) {
      let aElem = document.createElement('a');
      aElem.href = configs.HOST + '/admin/excel/studentModel.xlsx';
      var evt = document.createEvent("MouseEvents");
      evt.initEvent("click", true, true);
      aElem.dispatchEvent(evt);
    } else {
      message.error('~')
    }
  }
  const saveFile = (file: File) => {
    let formData = new FormData();
    formData.append('file', file);
    setFile(formData)
  }
  const columns:StandardTableColumnProps[] = [
    {
      title: '',
      dataIndex: 'name',
    },
  ]
  return (
    <>
      { console.log('file modal', file)}
      <Row>
        <Col><Button type="primary"></Button></Col>
      </Row>
      <Modal
        title=""
        width="60%"
        visible={false}
        centered
        footer={
          <div style={{ alignItems: 'center', justifyContent: 'center', display:'flex' }}>
            <Button type='primary' onClick={handleSubmit} disabled={!file}></Button>
          </div>
        }
      >
        <Alert
          message=""
          description={
            <>
              <p><b>1.</b>Excel</p>
              <p><b>2.</b>100010M</p>
              <p><b>3.</b>xlsxlsx</p>
            </>
          }
          type="info"
        />

        <div style={{marginTop:20, marginBottom: 10}}>
          <a href="" style={{marginRight:20}} onClick={downloadTemplateFile}><Icon type="file" />Excel</a>
          <span>(Excel)</span>
        </div>
        {
          upStatus.uploaded ?
            <>
              <div>{upStatus.totalNum}, {upStatus.successNum} {upStatus.failNum}</div>
              <StandardTable
                rowKey='id'
                columns={columns}
                data={{
                  list: [{name: '123', key:1}],
                  pagination: {}
                }}
              />
            </> :
          <MyDropzone saveFile={saveFile}/>
        }
      </Modal>
    </>
  )
}
export default Record;

